@import "variables";


.stories {
    display: flex;
    align-content: center;
    background-color: $background-white;
    border: 1px solid $border-color-grey;
    border-radius: 3px;
    margin: 2em 0 1.5em 0;
    height: 7.4em;
    overflow: hidden;
}

@media only screen and (max-width: 640px) {
    .strories {
        margin: 0 0 0.5em 0;
    }
}